/* ========================= */
/* CONTENEDOR PRINCIPAL */
/* ========================= */

.admin-container {
    max-width: 900px;
    margin: 40px auto;
    background: white;
    border-radius: 12px;
    padding: 30px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    animation: slideIn 0.3s ease-out;
    box-sizing: border-box;
    overflow-x: hidden;
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

/* ========================= */
/* HEADER */
/* ========================= */

.admin-header {
    display: flex;
    align-items: center;
    gap: 15px;
    border-bottom: 2px solid #eee;
    padding-bottom: 15px;
    margin-bottom: 20px;
}

.admin-header h2 {
    color: #1f2937;
    margin: 0;
}

.back-btn {
    background: none;
    border: none;
    cursor: pointer;
    color: #1f2937;
    padding: 5px;
    border-radius: 50%;
    transition: background 0.2s;
}

.back-btn:hover {
    background: #f0f0f0;
}

/* ========================= */
/* FORMULARIO CREAR ROL */
/* ========================= */

.new-role-form-container {
    padding: 20px;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    margin-bottom: 25px;
    background-color: #f9fafb;
}

.new-role-form-container h3 {
    margin-top: 0;
    margin-bottom: 15px;
    color: #1f2937;
    font-size: 1.2em;
}

.new-role-form {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
}

.new-role-form label {
    font-weight: 600;
    color: #4b5563;
    margin-bottom: 5px;
    display: block;
}

.new-role-form .input-group {
    display: flex;
    width: 100%;
}

.role-prefix {
    background-color: #e0e7ff;
    color: #4338ca;
    padding: 8px 10px;
    border: 1px solid #c7d2fe;
    border-right: none;
    border-radius: 6px 0 0 6px;
    font-weight: bold;
    font-size: 14px;
    white-space: nowrap;
}

.new-role-form input[type="text"],
.new-role-form textarea {
    width: 100%;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    font-size: 14px;
    color: #1f2937;
    min-height: 40px;
    box-sizing: border-box;
}

.new-role-form input[type="text"] {
    border-radius: 0 6px 6px 0;
}

.new-role-form textarea {
    border-radius: 6px;
    resize: vertical;
}

.new-role-form input:focus,
.new-role-form textarea:focus {
    border-color: #4338ca;
    box-shadow: 0 0 0 3px rgba(67, 56, 202, 0.1);
    outline: none;
}

.btn-create-role {
    background-color: #4338ca;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
    width: 100%;
}

.btn-create-role:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* ========================= */
/* GESTIÓN DE ROLES */
/* ========================= */

.role-management-container {
    margin-top: 30px;
    overflow-x: auto;
}

.role-management-table {
    width: 100%;
    min-width: 600px;
    border-collapse: collapse;
}

.role-management-table th,
.role-management-table td {
    padding: 10px 15px;
    border-bottom: 1px solid #eee;
    text-align: left;
}

.role-management-table th {
    background-color: #f7f7f7;
    font-weight: bold;
}

.protected-role-tag {
    background-color: #e5e5e5;
    color: #555;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.85em;
    font-weight: 600;
}

.btn-delete-role {
    background-color: #dc2626;
    color: white;
    padding: 6px 12px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    gap: 5px;
}

.btn-delete-role:hover {
    background-color: #b91c1c;
}

/* ========================= */
/* LISTA DE USUARIOS */
/* ========================= */

.user-list {
    margin-top: 30px;
    overflow-x: auto;
}

.user-list table {
    width: 100%;
    min-width: 700px;
    border-collapse: collapse;
}

.user-list th,
.user-list td {
    padding: 12px 15px;
    border-bottom: 1px solid #ddd;
    text-align: left;
}

.user-list th {
    background-color: #f7f7f7;
    font-weight: bold;
}

.user-list tr.editing {
    background-color: #fffbe6;
}

.role-checkboxes {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.role-checkboxes label {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
}

/* ========================= */
/* BOTONES */
/* ========================= */

.user-list td:last-child button {
    margin-right: 8px;
    padding: 8px 14px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    font-size: 14px;
}

.btn-edit {
    background-color: #1f2937;
    color: white;
}

.btn-save {
    background-color: #0b3d0b;
    color: white;
}

.btn-cancel {
    background-color: #8b0000;
    color: white;
}

/* ========================= */
/* RESPONSIVE MÓVIL */
/* ========================= */

@media (max-width: 768px) {
    .admin-container {
        margin: 16px auto;
        padding: 16px;
        width: 100%;
        max-width: calc(100% - 16px);
    }

    .admin-header {
        flex-wrap: wrap;
    }

    .admin-header h2 {
        font-size: 1.1em;
    }
}
