/* ========================================================================== */
/* 0. RESET & GLOBAL */
/* ========================================================================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

/* ========================================================================== */
/* 1. LAYOUT GENERAL */
/* ========================================================================== */
.profile-page {
  display: flex;
  min-height: 100vh;
  background-color: #f7f7f7;
  width: 100%;
  position: relative;
  overflow-x: hidden;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* ========================================================================== */
/* 2. SIDEBAR (Escritorio por defecto) */
/* ========================================================================== */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 260px;
  height: 100vh;
  background-color: #111827; /* Negro Azulado Profundo */
  color: white;
  z-index: 1201;
  box-shadow: 2px 0 15px rgba(0, 0, 0, 0.2);
  transform: translateX(0); /* Visible en PC */
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
}

.sidebar ul {
  list-style: none;
  padding: 0; /* Eliminado el padding superior que dejaba el hueco */
  margin: 0;
}

.sidebar li {
  padding: 18px 24px; /* Un poco más de aire vertical */
  cursor: pointer;
  font-weight: 600;
  font-size: 0.95rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.2s ease;
  color: #d1d5db;
  display: flex;
  align-items: center;
}

.sidebar li:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  padding-left: 30px;
}

.sidebar li.active {
  background-color: #D4AF37; /* Dorado Corporativo */
  color: #111827;
  box-shadow: inset 4px 0 0 white;
}

/* Ocultar botones de control en escritorio */
.mobile-menu-btn, .close-menu-btn {
  display: none;
}

/* ========================================================================== */
/* 3. CONTENIDO PRINCIPAL */
/* ========================================================================== */
.content {
  margin-left: 260px; /* Igual al ancho de la sidebar */
  padding: 40px;
  width: calc(100% - 260px);
  min-height: 100vh;
  box-sizing: border-box;
}

/* ========================================================================== */
/* 4. RESPONSIVE MÓVIL */
/* ========================================================================== */
@media (max-width: 768px) {
  .mobile-menu-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 15px;
    left: 15px;
    z-index: 1100;
    background: #111827;
    color: #D4AF37;
    border: none;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 10px rgba(0,0,0,0.2);
  }

  .sidebar {
    transform: translateX(-100%);
  }

  .sidebar.open {
    transform: translateX(0);
  }

  /* El botón de cerrar en móvil sí necesita un poco de espacio arriba */
  .close-menu-btn {
    display: block;
    align-self: flex-end;
    background: none;
    border: none;
    color: white;
    padding: 20px;
    cursor: pointer;
  }

  .content {
    margin-left: 0;
    width: 100%;
    padding: 20px;
    padding-top: 80px;
  }
}

/* ========================================================================== */
/* 5. OVERLAY */
/* ========================================================================== */
.menu-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(2px);
  z-index: 1200;
}