/* ========================================================================== */
/* DATOS PERSONALES - Estructura Principal */
/* ========================================================================== */

.user-profile {
  width: 100%;
  max-width: 1000px;
  background: white;
  border-radius: 12px;
  padding: 40px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  box-sizing: border-box;
}

.user-profile h2 {
  color: #1f2937;
  margin-top: 0;
  margin-bottom: 30px;
  border-bottom: 2px solid #eee;
  padding-bottom: 15px;
  font-size: 1.8rem;
}

.field {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  width: 100%;
}

.field label {
  font-size: 15px;
  color: #4b5563;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: bold;
}

/* Indicador visual de campo obligatorio */
.required-label::after {
  content: "*";
  color: #ef4444;
  margin-left: 4px;
}

/* Inputs y Spans */
.field input, .display-value {
  width: 100%; 
  padding: 12px 15px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  box-sizing: border-box;
  font-size: 16px;
  display: block;
  min-height: 48px;
}

.field input:focus {
  border-color: #32cd32;
  outline: none;
  box-shadow: 0 0 0 3px rgba(50, 205, 50, 0.1);
}

/* SOLUCIÓN AL DESBORDE EN MÓVIL (SPANS) */
.display-value {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  color: #374151;
  word-wrap: break-word;       /* Rompe palabras largas */
  overflow-wrap: break-word;
  white-space: normal;         /* Permite saltos de línea */
  height: auto;                /* Crece si el texto es largo */
}

/* Botones */
.profile-buttons {
  display: flex;
  gap: 15px;
  margin-top: 30px;
}

.profile-buttons button {
  padding: 12px 35px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
}

.btn-edit { background: #1f2937; }
.btn-save { background: #0b3d0b; }
.btn-cancel { background: #8b0000; }

.btn-edit:hover, .btn-save:hover, .btn-cancel:hover {
  filter: brightness(1.2);
  transform: translateY(-1px);
}

/* ========================================================================== */
/* RESPONSIVE MÓVIL */
/* ========================================================================== */
@media (max-width: 768px) {
  .user-profile {
    padding: 20px;
    border-radius: 0;
  }
  
  .profile-buttons {
    flex-direction: column;
  }

  .field label {
    font-size: 16px;
  }

  /* Ajuste de inputs para dedos en móvil */
  .field input, .display-value {
    font-size: 17px;
    padding: 14px;
  }

  .profile-buttons button {
    width: 100%;
    padding: 16px;
  }
}