.register {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f4f6;
  padding: 10px;
  box-sizing: border-box;
}

.register-container {
  background: white;
  padding: 30px 20px;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  box-sizing: border-box;
}

.register-container h2 {
  font-size: 26px;
  margin-bottom: 20px;
  text-align: center;
}

.register-container input:not([type="checkbox"]) {
  width: 100%;
  padding: 16px;
  margin-bottom: 12px;
  border: 1.5px solid #d1d5db;
  border-radius: 12px;
  font-size: 16px;
  box-sizing: border-box;
}

.password-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 12px;
}

.password-wrapper input { margin-bottom: 0 !important; }

.eye-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  display: flex;
  color: #6b7280;
}

.register-container button {
  width: 100%;
  padding: 16px;
  background-color: #1f2937;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

/* Botón volver */
.back-to-login {
  display: block;
  width: 100%;
  padding: 16px;
  margin-top: 15px;
  background: #e5e7eb;
  color: #1f2937;
  border-radius: 12px;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  box-sizing: border-box;
}

.error-box {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 15px;
  font-size: 14px;
  box-sizing: border-box;
}

.error-box { background: #fee2e2; border-left: 4px solid #ef4444; color: #991b1b; }