/* APPOINTMENT LIST COMPONENT STYLES */

.appointments-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  animation: fadeIn 0.3s ease-in-out;
}

.appointment-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
  background: #f9fafb;
  border-radius: 10px;
  border-left: 5px solid #d1d5db;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 100%;
  gap: 10px;
}

.appointment-card:hover {
  transform: translateX(4px);
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

/* Estados de la tarjeta (Coherente con Spring Boot Enum) */
.appointment-card.confirmed { border-left-color: #10b981; }
.appointment-card.pending { border-left-color: #f59e0b; }
.appointment-card.cancelled { 
  border-left-color: #ef4444; 
  opacity: 0.7; 
  background: #fff5f5;
}

.card-info-compact {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
}

.date-badge {
  display: flex;
  flex-direction: column;
  min-width: 95px;
  border-right: 1px solid #e5e7eb;
  padding-right: 10px;
}

.date-text {
  font-size: 0.75rem;
  color: #6b7280;
  display: flex;
  align-items: center;
  gap: 4px;
}

.time-text {
  font-weight: 700;
  color: #111827;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 4px;
}

.service-details h4 {
  margin: 0;
  font-size: 1rem;
  color: #1f2937;
}

.client-small {
  font-size: 0.8rem;
  color: #6b7280;
  display: flex;
  align-items: center;
  gap: 4px;
}

/* Status Tags */
.status-tag {
  font-size: 0.65rem;
  font-weight: 800;
  padding: 4px 10px;
  border-radius: 20px;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  white-space: nowrap;
}

.status-tag.confirmed { background: #d1fae5; color: #065f46; }
.status-tag.pending { background: #fef3c7; color: #92400e; }
.status-tag.cancelled { background: #fee2e2; color: #991b1b; }

/* Botón Cancelar */
.btn-icon-cancel {
  background: none;
  border: none;
  color: #ef4444;
  font-size: 1.3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px;
  border-radius: 50%;
  transition: background 0.2s;
  margin: 0;
}

.btn-icon-cancel:hover {
  background: #fee2e2;
}

/* Estado Vacío / No Auth */
.empty-state-compact, .auth-required-notice {
  text-align: center;
  padding: 40px 20px;
  color: #6b7280;
  background: #f3f4f6;
  border-radius: 12px;
  border: 2px dashed #d1d5db;
}

.auth-required-notice {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  color: #4b5563;
}

.auth-required-notice p {
  font-weight: 600;
  font-size: 0.9rem;
}

.card-actions-compact {
  display: flex;
  align-items: center;
  gap: 12px;
  justify-content: flex-end;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Responsive para tarjetas */
@media (max-width: 480px) {
  .appointment-card {
    flex-direction: column;
    align-items: flex-start;
  }
  .card-actions-compact {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #e5e7eb;
    padding-top: 10px;
  }
}