.login {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #f3f4f6;
  box-sizing: border-box;
}

.login-container {
  background: white;
  padding: 40px 30px;
  border-radius: 24px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
  animation: fadeInLogin 0.4s ease-out;
}

.login-container h2 {
  font-size: 26px;
  margin-bottom: 25px;
  color: #1f2937;
}

/* Contenedor relativo para el icono del ojo */
.password-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 16px;
}

.login-container input {
  width: 100%;
  padding: 14px;
  border: 1.5px solid #e5e7eb;
  border-radius: 12px;
  font-size: 16px;
  box-sizing: border-box;
  transition: all 0.2s;
}

.login-container input[type="text"] {
  margin-bottom: 16px;
}

.login-container input:focus {
  border-color: #1f2937;
  outline: none;
  box-shadow: 0 0 0 3px rgba(31, 41, 55, 0.1);
}

/* Estilo del icono del ojo */
.eye-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #6b7280;
  display: flex;
  align-items: center;
}

.login-container button {
  width: 100%;
  padding: 14px;
  background-color: #1f2937;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  margin-top: 10px;
}

.login-actions {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.login-link {
  color: #4b5563;
  font-size: 15px;
  text-decoration: none;
}

.error-box, .login-message {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 15px;
  font-size: 14px;
  box-sizing: border-box;
}

.error-box { background: #fee2e2; border-left: 4px solid #ef4444; color: #991b1b; }
.login-message { background: #dcfce7; border-left: 4px solid #10b981; color: #166534; }

@media (max-width: 480px) {
  .login-container { padding: 30px 20px; }
}

@keyframes fadeInLogin {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.login {
  position: relative; /* Importante para posicionar el hijo */
}

.login-lang-selector {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  gap: 5px;
  background: white;
  padding: 5px 10px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.login-lang-selector select {
  border: none;
  background: transparent;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}

.pending-booking-notice {
  background-color: #e3f2fd;
  color: #1976d2;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  border: 1px solid #bbdefb;
}

/* Botón Volver (Posicionado arriba a la izquierda) */
.login-back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
  color: #1f2937;
  font-weight: 600;
  background: white;
  padding: 8px 16px;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  transition: all 0.2s ease;
}

.login-back-btn:hover {
  background: #f9fafb;
  transform: translateX(-3px);
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

/* Aseguramos que el login tenga posición relativa */
