.register { /* Usando la clase del contenedor de tu JS */
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #f3f4f6;
  box-sizing: border-box;
}

.register-container {
  background: white;
  padding: 40px 30px;
  border-radius: 24px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.register-container h2 {
  font-size: 24px;
  margin-bottom: 25px;
}

.password-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
}

.register-container input {
  width: 100%;
  padding: 14px;
  border: 1.5px solid #e5e7eb;
  border-radius: 12px;
  font-size: 16px;
  box-sizing: border-box;
}

.eye-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #6b7280;
}

.register-container button {
  width: 100%;
  padding: 14px;
  background-color: #1f2937;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 700;
  margin-top: 10px;
}

@media (max-width: 480px) {
  .register-container { padding: 30px 20px; }
}

.error-box {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 15px;
  font-size: 14px;
  box-sizing: border-box;
}

.error-box { background: #fee2e2; border-left: 4px solid #ef4444; color: #991b1b; }