/* Contenedor del Panel (La tarjeta) */
.booking-panel-card {
  background: #ffffff;
  padding: 24px;
  border-radius: 15px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  
  /* --- CAMBIOS PARA QUE SEA STICKY --- */
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
  height: fit-content;
  z-index: 10;
}

/* Indicador de fecha seleccionada */
.selected-date-display {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #f3f4f6;
  padding: 10px 15px;
  border-radius: 8px;
  margin: 15px 0;
  color: #374151;
  font-size: 0.9rem;
  border-left: 4px solid #10b981;
}

/* Grupos del formulario */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 8px;
}

.form-group select {
  width: 100%;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 1rem;
  color: #111827;
  background-color: #f9fafb;
  transition: all 0.2s;
}

.form-group select:focus {
  outline: none;
  border-color: #10b981;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
  background-color: #fff;
}

/* Estados de Error y Ocupado */
.input-error {
  border-color: #ef4444 !important;
  background-color: #fef2f2 !important;
}

.error-text-small {
  color: #dc2626;
  font-size: 0.8rem;
  margin-top: 8px;
  font-weight: 500;
}

.error-alert {
  background-color: #fee2e2;
  color: #b91c1c;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 15px;
  font-size: 0.85rem;
  border: 1px solid #fca5a5;
  text-align: center;
}

/* Botón de Confirmación */
.confirm-booking-btn {
  width: 100%;
  padding: 14px;
  background-color: #111827;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s;
}

.confirm-booking-btn:hover:not(:disabled) {
  background-color: #374151;
  transform: translateY(-1px);
}

.confirm-booking-btn:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

/* Animación de brillo */
@keyframes highlight-glow {
  0% { border-color: #e5e7eb; }
  50% { border-color: #10b981; box-shadow: 0 0 15px rgba(16, 185, 129, 0.2); }
  100% { border-color: #e5e7eb; }
}

.panel-highlight {
  animation: highlight-glow 0.6s ease-out;
}

/* Cuadro de advertencia cuando ya existe una cita */
.warning-box {
  background-color: #fffbeb;
  border: 1px solid #fcd34d;
  color: #92400e;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 12px;
  margin-top: 10px;
}

.warning-box p {
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0;
  font-weight: 500;
}

.warning-box svg {
  color: #f59e0b;
}

.client-type-toggle {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.client-type-toggle button {
  flex: 1;
  padding: 10px;
  border: 1px solid #d1d5db;
  background: white;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.client-type-toggle button.active {
  background: #111827;
  color: white;
  border-color: #111827;
}

.external-fields-grid {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
}

.external-fields-grid input {
  width: 100%;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
}

/* Desactivar sticky en pantallas pequeñas para que no moleste */
@media (max-width: 1100px) {
  .booking-panel-card {
    position: static;
  }
}

/* La clase que añadimos en el JS */
.booking-panel-card.panel-highlight {
  border-color: #3b82f6; /* Un azul vibrante o tu color de marca */
  box-shadow: 0 0 15px rgba(59, 130, 246, 0.3);
  transform: translateY(-2px);
  transition: all 0.3s ease;
}

.booking-panel-card {
  transition: all 0.5s ease; /* Transición suave de vuelta al estado original */
  border: 1px solid #e2e8f0;
}

/* Opcional: Animación de texto */
.date-pop {
  display: inline-block;
  animation: pop 0.4s ease-out;
}

@keyframes pop {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); color: #3b82f6; }
  100% { transform: scale(1); }
}

/* Ajuste de la rejilla de campos externos */
.external-fields-grid {
  display: flex;
  flex-direction: column;
  gap: 2px; /* Reducimos gap porque los form-group ya tienen margin-bottom */
  margin-bottom: 10px;
  animation: fadeIn 0.3s ease-out;
}

/* Estilo para los inputs de texto/tel/email en el panel */
.admin-input, 
.external-fields-grid input {
  width: 100%;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 1rem;
  color: #111827;
  background-color: #f9fafb;
  transition: all 0.2s;
  font-family: inherit;
}

.admin-input:focus,
.external-fields-grid input:focus {
  outline: none;
  border-color: #10b981; /* Verde esmeralda como tus otros botones */
  background-color: #fff;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}

/* Animación de aparición suave al cambiar de tipo de usuario */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Mejora visual de los botones de alternancia (Registrado vs Externo) */
.client-type-toggle button {
  flex: 1;
  padding: 12px;
  border: 1px solid #e5e7eb;
  background: #f9fafb;
  color: #6b7280;
  font-weight: 500;
  transition: all 0.2s;
}

.client-type-toggle button.active {
  background: #111827;
  color: #D4AF37; /* Dorado corporativo */
  border-color: #111827;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}