/* ========================================================================== */
/* 0. RESET Y CORRECCIONES GLOBALES */
/* ========================================================================== */
* {
  box-sizing: border-box;
}

/* ========================================================================== */
/* 1. LAYOUT GLOBAL */
/* ========================================================================== */
.booking-layout {
  display: flex;
  flex-direction: column;
  /* Organiza secciones en vertical */
  align-items: center;
  gap: 30px;
  padding: 20px;
  background-color: #f8f9fa;
  min-height: 100vh;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

/* Contenedor para las 3 columnas superiores */
.booking-upper-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  width: 100%;
}

/* ========================================================================== */
/* 2. CONTENEDORES PRINCIPALES (Tarjetas Blancas) */
/* ========================================================================== */
.calendar-container,
.details-container,
.form-panel-container,
.admin-services-bottom-section {
  background: white;
  padding: 20px;
  border-radius: 14px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  position: relative;
}

.calendar-container {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
}

.form-panel-container {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
}

.details-container {
  flex: 1.5;
  min-width: 300px;
  max-width: 550px;
  max-height: 85vh;
  overflow-y: auto;
}

.admin-services-bottom-section {
  width: 100%;
  margin-top: 10px;
}

/* ========================================================================== */
/* 3. CALENDARIO (React-Calendar) */
/* ========================================================================== */
.calendar-title {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #111827;
  text-align: center;
}

.custom-calendar {
  width: 100% !important;
  border: none !important;
  border-radius: 12px;
  font-family: inherit;
}

.sunday-red {
  color: #ef4444 !important;
  font-weight: bold;
}

.holiday-tile {
  background-color: #fee2e2 !important;
  color: #dc2626 !important;
  border-radius: 50%;
  font-weight: bold;
}

.dot-indicator {
  height: 6px;
  width: 6px;
  background: #10b981;
  border-radius: 50%;
  margin: 2px auto 0;
}

.exception-icon {
  color: #f59e0b;
  margin-left: 2px;
}

/* ========================================================================== */
/* 4. GESTIÓN DE ADMIN (Días y Horarios) */
/* ========================================================================== */
.admin-day-manager {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.btn-toggle-day {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s;
}

.btn-toggle-day.is-open {
  background: #111827;
  color: #D4AF37;
  border: 1px solid #D4AF37;
}

.btn-toggle-day.is-closed {
  background: #10b981;
  color: white;
}

.btn-edit-schedule {
  width: 100%;
  padding: 10px;
  background: white;
  color: #111827;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-weight: 600;
  cursor: pointer;
}

.btn-edit-schedule:hover {
  background: #f9fafb;
}

.closed-notice {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  background: #fff5f5;
  border: 2px dashed #feb2b2;
  border-radius: 12px;
  color: #c53030;
  margin-bottom: 20px;
  text-align: center;
}

/* ========================================================================== */
/* 5. SERVICE MANAGER (GESTIÓN DE TARIFAS) */
/* ========================================================================== */
.services-grid-internal {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 15px;
  margin-top: 15px;
}

.service-card-compact {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 15px;
}

.input-invisible-name {
  width: 100%;
  border: 1px solid transparent;
  background: transparent;
  font-weight: 700;
  padding: 4px;
  font-size: 1rem;
}

.input-invisible-name:focus {
  background: white;
  border-color: #d1d5db;
}

.card-secondary-row {
  display: flex;
  gap: 10px;
  margin: 10px 0;
}

.input-group-compact {
  display: flex;
  align-items: center;
  gap: 4px;
  background: white;
  border: 1px solid #d1d5db;
  padding: 4px 8px;
  border-radius: 6px;
}

.add-service-form-compact {
  background: #fff;
  border: 2px solid #f3f4f6;
  border-radius: 12px;
  padding: 20px;
  margin-top: 20px;
}

.form-inline-row {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.btn-add-full {
  width: 100%;
  background: #10b981;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 8px;
  font-weight: 700;
  cursor: pointer;
}

/* ========================================================================== */
/* 6. MODALES Y UTILIDADES */
/* ========================================================================== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  backdrop-filter: blur(4px);
}

.schedule-modal {
  background: white;
  padding: 30px;
  border-radius: 16px;
  width: 90%;
  max-width: 400px;
}

.spinner {
  animation: spin 1s linear infinite;
  font-size: 2rem;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* ========================================================================== */
/* 7. RESPONSIVE */
/* ========================================================================== */
@media (max-width: 1100px) {
  .booking-upper-grid {
    flex-direction: column;
    align-items: center;
  }

  .calendar-container,
  .details-container,
  .form-panel-container {
    width: 100%;
    max-width: 600px;
  }
}

@media (max-width: 480px) {
  .appointment-card {
    flex-wrap: wrap;
  }

  .card-actions-compact {
    width: 100%;
    justify-content: flex-end;
    border-top: 1px solid #eee;
    padding-top: 8px;
  }

  .form-inline-row {
    flex-direction: column;
  }
}

/* ========================================================================== */
/* EXTRA: CSS PARA MODAL DE HORARIOS */
/* ========================================================================== */

.schedule-modal {
  background: white;
  padding: 25px;
  border-radius: 20px;
  width: 95%;
  max-width: 350px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.2);
  animation: modalIn 0.3s ease-out;
}

@keyframes modalIn {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.modal-header-simple {
  text-align: center;
  margin-bottom: 20px;
}

.modal-header-simple h3 {
  margin: 10px 0 5px 0;
  color: #111827;
}

.selected-date-display {
  color: #D4AF37; /* Dorado para resaltar */
  font-weight: 700;
  margin: 0;
}

.modal-icon {
  font-size: 2.5rem;
  color: #D4AF37;
}

.modal-body-schedule {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 25px;
}

.form-group-compact {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.form-group-compact label {
  font-size: 0.85rem;
  font-weight: 600;
  color: #4b5563;
}

.schedule-input {
  padding: 12px;
  border: 1.5px solid #e5e7eb;
  border-radius: 10px;
  font-size: 1.1rem;
  font-family: inherit;
  color: #111827;
  outline: none;
  transition: border-color 0.2s;
}

.schedule-input:focus {
  border-color: #D4AF37;
}

.modal-actions-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.btn-modal-cancel, .btn-modal-save {
  padding: 12px;
  border-radius: 10px;
  border: none;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-modal-cancel {
  background: #f3f4f6;
  color: #4b5563;
}

.btn-modal-save {
  background: #111827;
  color: #D4AF37;
  border: 1px solid #D4AF37;
}

.btn-modal-save:hover {
  background: #1f2937;
}