.forgot-password {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f4f6;
  padding: 15px;
  box-sizing: border-box;
}

.forgot-password-container {
  background: white;
  padding: 35px 25px;
  border-radius: 20px;
  width: 100%;
  max-width: 400px;
  text-align: center;
  box-sizing: border-box;
}

.forgot-password-container input {
  width: 100%;
  padding: 16px;
  margin: 15px 0;
  font-size: 16px;
  border: 1.5px solid #d1d5db;
  border-radius: 12px;
  box-sizing: border-box;
}

.forgot-password-container button {
  width: 100%;
  padding: 16px;
  font-size: 18px;
  background-color: #1f2937;
  color: white;
  border-radius: 12px;
  font-weight: bold;
  border: none;
}

/* ARREGLO VOLVER FORGOT */
.forgot-password-container .back-to-login {
  display: block;
  width: 100%;
  padding: 16px;
  margin-top: 15px;
  background: #e5e7eb;
  color: #1f2937;
  border-radius: 12px;
  text-decoration: none;
  font-weight: bold;
  box-sizing: border-box;
}

.error-box {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 15px;
  font-size: 14px;
  box-sizing: border-box;
}

.error-box { background: #fee2e2; border-left: 4px solid #ef4444; color: #991b1b; }